import React, { Component } from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import { Button, Layout, Menu, Row, Col, Icon } from "antd";
import "./index.css";
import Footer from "./components/footer";
import { MENU_ITEMS, HEADER_BOUNCE } from "./data";

//Components
import { Animated } from "react-animated-css";
import { Parallax } from "react-parallax";

//Routes
import ModalSlide from "./forms/banner/index";
import Index from "module/main/index";
import Plans from "./module/plans/index";
import Size from "./module/plans/screens/second_section";
import Days from "./module/plans/screens/third_section";

//Components Antd
import BreakfastTable from "./admin/plan_meals/screens/breakfast";
import BreakfastDrawer from "./admin/plan_meals/forms/breakfast_form";

const { Header, Content, Sider } = Layout;
const { SubMenu } = Menu;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      collapsed: false,
      visible: false,
      hover: false,
      theposition: 0,
      visible_drawer: false,
      visible_table: false
    };
  }

  onClose = () => {
    this.setState({
      visible_drawer: false
    });
  };

  onClosetable = () => {
    this.setState({
      visible_table: false
    });
  };

  onCollapse = collapsed => {
    this.setState({ collapsed });
  };

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    this.setState({
      visible: false
    });
  };

  handleCancel = e => {
    this.setState({
      visible: false
    });
  };

  componentDidUpdate(props, state) {
    const scrollPosition = window.pageYOffset;
    if (state.scroll !== scrollPosition) {
      this.setState({ scroll: scrollPosition });
    }
  }

  componentDidMount() {
    window.addEventListener("scroll", this.listenToScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.listenToScroll);
  }

  listenToScroll = () => {
    const winScroll =
      document.body.scrollTop || document.documentElement.scrollTop;

    const height =
      document.documentElement.scrollHeight -
      document.documentElement.clientHeight;

    const scrolled = winScroll / height;

    this.setState({
      theposition: scrolled
    });
  };

  render() {
    return (
      <Layout>
   
        <Layout
          style={
          
             { marginLeft: "0px", padding: "0px 100px" }
          }
        >
          <Header
            style={
              {
                    position: "fixed",
                    zIndex: 999,
                    width: "100%"
                  }
            }
          >
            <Parallax
              strength={200}
              className= "parallax-content"
            >
              <div className="header-logo" />

              <Menu
                mode="horizontal"
                defaultSelectedKeys={["1"]}
                style={
                  this.state.scroll !== 0
                    ? {
                        lineHeight: "64px",
                        background: "white",
                        paddingTop: "30px",
                        fontWeight: "bolder"
                        // transition: '2s',
                      }
                    : {
                        fontWeight: "bolder",
                        lineHeight: "64px",
                        background: "transparent",
                        paddingTop: "30px",
                        borderBottom: "1px solid transparent"
                      }
                }
              >
                <Menu.Item style={{ float: "right" }}>
                  <a
                    style={{ color: "black", border: "1px solid transparent" }}
                  >
                    <Icon
                      style={{
                        marginTop: "20px",
                        float: "right",
                        fontSize: "20px"
                      }}
                      type="shopping-cart"
                    />
                  </a>
                </Menu.Item>

                <Menu.Item style={{ float: "right" }}>
                  <Button
                    style={{
                      width: "120px",
                      background: "#6ACBDF",
                      color: "white",
                      border: "1px solid #6ACBDF"
                    }}
                  >
                    SIGN UP
                  </Button>
                </Menu.Item>

                {MENU_ITEMS.map(items => {
                  return (
                    <Menu.Item
                      onMouseEnter={() => this.setState({ hover: false })}
                      style={{
                        fontFamily: "Open Sans, sans-serif",
                        float: "right",
                        opacity: "100%",
                        color: "#707070"
                      }}
                      key={items.key}
                      values={items.key}
                    >
                      <a>{items.title}</a>
                    </Menu.Item>
                  );
                })}

                <Menu.Item
                  style={{ float: "right" }}
                  onMouseEnter={() => this.setState({ hover: true })}
                >
                  ORDER NOW
                </Menu.Item>
              </Menu>
            </Parallax>
          </Header>

          <div
            className= {
              this.state.hover === false ? "header-menu" : " header-menu-not"
            }
          >
            <Row>
              <Col span={10}>
                <ul
                  style={{
                    listStyle: "none",
                    opacity: 1,
                    paddingTop: "50px",
                    marginLeft: "250px"
                  }}
                >
                  {HEADER_BOUNCE.map(item => {
                    return (
                      <li>
                        <a
                          href={item.url}
                          style={{ color: "white", fontSize: "16px" }}
                          onClick={() => this.setState({ hover: false })}
                        >
                          <Animated animationOut="bounce" isVisible={false}>
                            {item.title}
                          </Animated>
                        </a>
                      </li>
                    );
                  })}
                </ul>
              </Col>

              <Col span={4}>
                <Animated animationOut="bounce" isVisible={false}>
                  <ul
                    style={{
                      listStyle: "none",
                      opacity: 1,
                      paddingTop: "50px",
                      fontSize: "16px"
                    }}
                  >
                    <li>
                      <a style={{ color: "white" }}> VEGETARIAN </a>
                    </li>
                    <li>
                      <a style={{ color: "white" }}> LOW CARB </a>
                    </li>
                    <li>
                      <a style={{ color: "white" }}> LUNCH PACKS </a>
                    </li>
                  </ul>
                </Animated>
              </Col>

              <Col span={4}>
                <Animated animationOut="bounce" isVisible={false}>
                  <ul
                    style={{
                      listStyle: "none",
                      opacity: 1,
                      paddingTop: "50px",
                      fontSize: "16px"
                    }}
                  >
                    <li>
                      <a style={{ color: "white" }}> COUPLE PLANS </a>
                    </li>
                  </ul>
                </Animated>
              </Col>
            </Row>
          </div>

          <Content
            style={{
              marginTop: "0px",
              zIndex: 99,
              borderTop: "28px solid #6acbdf"
            }}
            onMouseEnter={() => {
              this.setState({ hover: false });
            }}
          >
            <HashRouter>
              <Switch>
                <Route path="/" exact component={Index} />
                <Route path="/plans" exact component={Plans} />
                <Route path="/plans/size" exact component={Size} />
                <Route path="/plans/days" exact component={Days} />
              </Switch>
            </HashRouter>
          </Content>
          <Footer />
        </Layout>

        <ModalSlide
          visible={this.state.visible}
          handleOk={this.handleOk}
          handleCancel={this.handleCancel}
        />

        <BreakfastDrawer
          visible={this.state.visible_drawer}
          onClose={this.onClose}
        />

        <BreakfastTable
          visible={this.state.visible_table}
          onClose={this.onClosetable}
          collapsed={this.state.collapsed}
        />
      </Layout>
    );
  }
}

export default App;
